.App {
  background-color: #000;
  min-height: 100vh;
  font-size: calc(10px + 1vmin);
  width: 100vw;
  height: 100vh;
  font-family: Arial, Helvetica, sans-serif;
  color: white;

}
.loading-icon{
  position: absolute;
  top:50%;
  left: 50%;
}
.team-banner{
  width:11vw;
  height: 90vh;
}
.icon-container{
  width: 80%;
  height: 84%;
  display:flex;
  flex-direction: column;
}

.team-banner .icon-container img{
  left: 0;
  width: 7vw;
  min-width: 110px;
  max-width: 130px;
  padding-top: 5vh;
  margin: 0 0 7vh 3vw;
  align-content: right;
}

header{
  font-size: 5.5vh;
  margin: 0vh 8vw 5vh 0;
  text-align:center;
  text-transform: uppercase;
  height:12vh;
  max-height:148px;
}
header .timestamp{
  margin-right:8vw;
  line-height: 0;
  font-weight:normal;
  font-size:1.5vh;
}
header .title{
  line-height: 0;
}

.App-container{
  display: flex;
  flex-direction: row;
  justify-content: center;
  width:100vw;

}
.App-pane{
  display:flex;
  flex-direction: column;
  max-width: 1600px;
  max-height: 900px;
  width:auto;
}

.components{
  display:flex;
  flex-direction: row;
}

.field-pane{
  background-image: url(resources/Field.png);
  background-size: 100% 100%;
  background-repeat: no-repeat;
  height:100%;
  width:17.5vw;
  max-height: 690px;
  max-width: 365px;
  min-height: 480px;
  min-width: 348px;
}

.sub-pane{
  font-size: 14px;
  height: 100%;
  width:30vw;
  max-height: 636px;
  max-width: 528px;
  margin-left:3vw;
  
  display:flex;
  flex-direction: column;
}
.sub-pane-container{
  display:flex;
  flex-direction: row;
}

.sub-pane .hometeam{
  text-align: left;
  width: 100%;
  margin-left: -55px;

}
.sub-pane .awayteam{
  text-align: right;
  width: 100%;
  margin-right: -7px;
}
.sub-pane .home-sub-player{
  display:flex;
  flex-direction: row;
  margin-top:1vh;
}
.sub-pane ul .player-number{
  width: 1vw;
}
.sub-pane .away-sub-player{
  display:flex;
  flex-direction: row-reverse;
}
.sub-pane ul .player-number{
  margin: 0 1.5vw 0 1.5vw;
}

.sub-banner{
  display: flex;
  flex-direction: row;
  margin-top:1vh;
  justify-content: space-between;

  margin-right:2%;
  font-weight: 700;
}
.banner-home-icon-container{
  width: 70px;
}
.banner-away-icon-container{
  width: 61px;

}

